import React from 'react'
import { Redirect, useParams } from 'react-router-dom'

const OLD_PATH_STRUCTURE = /^(0x[a-fA-F0-9]{40})-(0x[a-fA-F0-9]{40})$/

function RedirectOldRemoveLiquidityPathStructure2() {
  const { tokens } = useParams<{ tokens: string }>()
  if (!OLD_PATH_STRUCTURE.test(tokens)) {
    return <Redirect to="/pool2" />
  }
  const [currency0, currency1] = tokens.split('-')

  return <Redirect to={`/removeape/${currency0}/${currency1}`} />
}
export default RedirectOldRemoveLiquidityPathStructure2
